import { model, Schema, models } from 'mongoose';

export default function modules() {
  const collectionName = 'modules';
  if (models[collectionName]) {
    return model(collectionName);
  }

  const featureSchema = new Schema({
    featureName: {
      type: String,
      required: true,
    },
    featureId: {
      type: String,
      required: true,
    },
    divisionType: {
      type: String,
      required: true,
    },
  });

  const moduleSchema = new Schema({
    moduleName: {
      type: String,
      required: true,
    },
    description: {
      type: String,
      required: true,
    },
    features: {
      type: [featureSchema],
    },
  });

  return model(collectionName, moduleSchema);
}
